@import "styles/utils";

.button{
  padding-left: .5rem;
  padding-right: .5rem;
  padding-top: .25rem;
  padding-bottom: .25rem;
  font-weight: 600;
  font-size: .9rem;
  color:white;
  cursor: pointer;
  user-select: none;
  display: inline-flex;

  &.default{
    background: $oc-blue-6;
    &:hover{
      background: $oc-blue-5;
    }
    &:active{
      background: $oc-blue-6;
    }
  }

  &.gray{
    background: $oc-gray-7;
    &:hover{
      background: $oc-gray-6;
    }
    &:active{
      background: $oc-gray-7;
    }
  }

  &.outline{
    border: 2px solid white;
    border-radius: 2px;
    &:hover{
      background: white;
      color:$oc-blue-6;
    }
    &:active{
      background: rgba(255, 255, 255, 0.85);
      border: 2px solid rgba(255, 255, 255, 0.85);
    }
  }
  &:hover{
    @include material-shadow(2, 0.5);
  }
  &.disabled{
    background: $oc-gray-4;
    color:$oc-gray-6;
    cursor: default;
    &:hover, &:active{
      box-shadow: none;
      background: $oc-gray-4;
    }
  }

  & + & {
    margin-left: .5rem;
  }
}
